    }
    .form-section-title {
        font-size: 2.5rem;
    }
    .input-box, .add-item-search-box-wrapper {
        font-size: 2rem;
        width: 90%;
    }
    .form-button {
        font-size: 1.6em;
    }
    .form-text {
